import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { addTask } from '../actions'

class NewTask extends React.Component {

	constructor(props) {
		super(props)

		this.state = {
			tags:[],
			title : true
		}

		this.handleChange = this.handleChange.bind(this)
		this.handleSubmit = this.handleSubmit.bind(this)
		this.noSubmit = this.noSubmit.bind(this)

	}

	handleSubmit(e) {
		e.preventDefault()

		//Dispatch action!!!!

		console.log(this)

		this.props.addTask({
			title: this.state.title,
			goal : this.state.goal,
			tags : this.state.tags,
		})

		this.setState({
			title:'',
			goal:'',
			tags:[],
		})
	}

	isOn(e){
		e.preventDefault()
		e.isTagOn = 'tagIsOn'
	}

	handleChange(event) {
		this.setState({
			[event.target.name]: event.target.value
		})
	}

	noSubmit(e){
		e.preventDefault()
	}

	showTags(e){
		e.preventDefault()
	}




	render() {
		// let isTagOn = ''

		let isNull
		this.state.title ?  isNull = this.noSubmit : isNull = this.handleSubmit


		return (
			<div id="newTaskForm">
				<form onSubmit={isNull}>
					<label>
						Create Task
						<br></br>
						<textarea placeholder="Add Details ..." autoComplete="off" name="title" type="text" onChange={this.handleChange} value={this.state.title.value}  />
						<button id="tagButton" onClick={this.showTags}> + </button>
						<button type="submit"  onChange={ isNull }>submit</button>
					</label>
					{
					// 	<label>
					// 	Task Objective:
					// 	<input placeholder = "Task objective" autoComplete="off" type="text" name = "goal" onChange={this.handleChange} value={this.state.goal} />
					// </label>
				}

				</form>
			</div>
		)
	}
}

NewTask.propTypes = {
	addTask: PropTypes.func.isRequired,
	tags: PropTypes.object
}

export default connect(
	state => ({
		tasks: state.tasks,
		tags: state.tags
	}), {
		addTask,
	}
)(NewTask)



